<template>
<TransitionRoot appear :show="isOpen" as="template">
    <Dialog as="div" @close="toggleModal" class="relative z-10">
        <TransitionChild as="template" enter="duration-300 ease-out" enter-from="opacity-0" enter-to="opacity-100" leave="duration-200 ease-in" leave-from="opacity-100" leave-to="opacity-0">
            <div class="fixed inset-0 bg-black bg-opacity-25" />
        </TransitionChild>
        <div class="fixed inset-0 overflow-y-auto">
            <div class="flex min-h-full items-center justify-center p-4 text-center">
                <TransitionChild as="template" enter="duration-300 ease-out" enter-from="opacity-0 scale-95" enter-to="opacity-100 scale-100" leave="duration-200 ease-in" leave-from="opacity-100 scale-100" leave-to="opacity-0 scale-95">
                    <DialogPanel class="w-full max-w-xl transform overflow-hidden rounded-md bg-white p-6 text-left align-middle shadow-xl transition-all">
                        <div class=" flex justify-between">
                            <DialogTitle as="h3" class="text-lg font-medium leading-6 text-gray-900">
                                Driver {{driver?.first_name}} {{driver?.last_name}}
                            </DialogTitle>
                             </div>
                        <div class="mt-2">
                            <form class="flex text-sm font-base flex-col gap-3">
                                <div class="flex flex-col gap-1">
                                    <label for="first_name">First name</label>
                                    <input v-model="driver.first_name" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g Daniel ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="last_name">Last name</label>
                                    <input v-model="driver.last_name" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g Smith ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="email">Email</label>
                                    <input v-model="driver.email" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g Daniel ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="phone_number">Phone number</label>
                                    <input v-model="driver.phone_number" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g 0712345675 ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="id_number">Id number</label>
                                    <input v-model="driver.identity_number" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g 22776544 ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="licence_number">Licence number</label>
                                    <input v-model="driver.licence_number" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g 14564312 ">
                                </div>
                                <div class="flex flex-col gap-1">
                                    <label for="kra_pin">KRA pin</label>
                                    <input v-model="driver.kra_pin" type="text" class="bg-white border border-gray-200 focus:outline-none focus:right-0 py-2 shadow-sm rounded-md pl-3 placeholder:text-gray-500 font-base" placeholder="E.g A02567975U ">
                                </div>
                                <div>
                                    <button @click.prevent="updateDriverDetails" class="bg-violet-600 w-full text-center font-base text-white py-2 px-4 rounded-md">
                                        Update driver
                                    </button>
                                </div>
                            </form>
                        </div>
                    </DialogPanel>
                </TransitionChild>
            </div>
        </div>
    </Dialog>
</TransitionRoot>
</template>
<script>
import {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogPanel,
    DialogTitle,
} from '@headlessui/vue'
import {
    mapActions
} from 'vuex'

export default {
    components: {
        TransitionRoot,
        TransitionChild,
        Dialog,
        DialogPanel,
        DialogTitle,
    },
    data() {
        return {
            isOpen: false,
            driver: {
                first_name: "",
                last_name: "",
                email: "",
                phone_number: "",
                identity_number: "",
                licence_number: "",
                kra_pin: ""
            }

        }
    },
    methods: {
        ...mapActions({
            updateDriver: 'updateDriver',
            deleteDriver: 'deleteDriver',
        }),
        toggleModal() {
            this.isOpen = !this.isOpen
        },
        updateDriverDetails() {
            this.updateDriver({
                uuid: this.driver.id,
                payload: this.driver,
                cb: (() => {
                    this.toggleModal()
                    this.emitter.emit("reloadDrivers", "edit")
                })
            })
        }
    },
    mounted() {
        this.emitter.on("showDriverModal", value => {
            this.driver = value
            this.toggleModal()
        })
    }
}
</script>
